h1,h2,h3,h4,h5,h6{
  font-family: 'Roboto Mono', monospace !important;
}

*{
  font-family: 'Roboto', sans-serif;
}
a{
  color: #2794EB;
}
b{
  color: #000;
}
a[href]{
    cursor: pointer;
}
.hidden.menu {
    display: none;
}

.masthead.segment {
    /*min-height: 700px;*/
    height: auto;
    padding: 1em 0 10em 0;
    background-image: url("img/acm-bg.png") !important;
    background-position: top center !important;
    background-size: cover !important;
}
.masthead.segment #prelim-hero{
    margin-top: 4em;
}
.masthead.segment #onsite-hero{
    margin-top: 4em;
}
.masthead .logo.item img {
    margin-right: 1em;
}
.masthead .ui.menu .ui.button {
    margin-left: 0.5em;
}
.masthead h1.ui.header {
    margin-top: 0em;
    margin-bottom: 0em;
    font-size: 3em;
    font-weight: normal;
    text-align: right;
}
.masthead h2 {
    font-size: 1.7em;
    font-weight: normal;
}
.masthead .text.container{
  margin-left: auto !important;
  margin-right: auto !important;
}
.masthead .register.button{
  margin: 1em 0 1em 0;
}
.ui.vertical.stripe {
    padding: 8em 0em;
}
.ui.vertical.stripe h3 {
    font-size: 2em;
}
.ui.vertical.stripe .button + h3,
.ui.vertical.stripe p + h3 {
    margin-top: 3em;
}
.ui.vertical.stripe .floated.image {
    clear: both;
}
.ui.vertical.stripe p {
    font-size: 1.33em;
}
.ui.vertical.stripe .horizontal.divider {
    margin: 3em 0em;
}

.quote.stripe.segment {
    padding: 0em;
}
.quote.stripe.segment .grid .column {
    padding-top: 5em;
    padding-bottom: 5em;
}
.secondary.pointing.menu .toc.item {
    display: none;
}

@media only screen and (max-width: 700px) {
    .ui.fixed.menu {
        display: none !important;
    }
    .secondary.pointing.menu .item,
    .secondary.pointing.menu .menu {
        display: none;
    }
    .secondary.pointing.menu .toc.item {
        display: block;
        align-self: center;
    }
    .masthead.segment {
        height: 85vh;
        padding: 1em 0 4em 0;
        background-image: url("img/acm-bg-sm.png") !important;
        background-position: bottom center !important;
        background-size: cover !important;
    }
    .masthead h1.ui.header {
        font-size: 2em;
        margin-top: 0.2em;
        text-align: center;
        padding: 0 1em;
    }
    .masthead h2 {
        margin-top: 0.5em;
        font-size: 1.5em;
        text-align: center;
    }
    .masthead h3 {
        margin-top: 0.5em;
        font-size: 1em;
        text-align: center;
    }
    .masthead a{
        color: #045ba2;
    }
    .masthead .register.button{
        display: table;
        margin: 1em auto;
    }
    .masthead .secondary.pointing.menu h4{
        font-size: 0.8em;
    }
    .masthead .secondary.pointing.menu .icpc.logo{
        min-width: 50px;
        max-width: 50px;
        height: auto;
    }
    .icpc.logo.big{
        display: block;
        min-width:200px;
        min-height:200px;
        width:200px;
        background-position-x: center;
        margin-left: auto;
        margin-right: auto;
    }

    /*.committee dt{*/
        /*float: none !important;*/
    /*}*/
    /*.committee dd{*/
        /*margin-left: 150px !important;*/
    /*}*/
}

.main.side.container > .left.rail{
    margin-right: 3em !important;
    margin-left: 1em !important;
    padding-top: 2em;
    width: 200px;
}

.main.side.container {
    margin-left: 300px !important;
    position: relative;
    margin-right: 3em !important;
    width: auto !important;
    /*max-width: 960px !important;*/
}

/* Remove Rail */
@media only screen and (max-width: 992px) {

    /* Remove Rail */
    .main.side.container > .left.rail{
        display: none;
    }

    /* Fluid Containers */
    .masthead > .container,
    .main.side.container,
    .fixed.menu > .container {
        margin-left: 1em !important;
        margin-right: 1em !important;
        max-width: none !important;
    }

    /* Still 100% Fixed Menu */
    /*.fixed.menu > .container {*/
        /*margin-left: 0em !important;*/
        /*margin-right: 0em !important;*/
        /*width: 100% !important;*/
        /*max-width: none !important;*/
    /*}*/
}
.footer .sponsor a[href]{
    padding: 0;
    display: flex;
    align-items: center;
}
.footer .sponsor img{
    max-height: 120px;
    padding: 0;
    margin: 1.4rem;
    max-width: 190px;
}
.footer .sponsor img.large{
    max-height: 140px;
    max-width:250px;
}
.footer.segment {
    padding: 3em 0;
}
.footer .header{
  color: #777 !important;
  text-transform: uppercase;
}
.footer p{
  color: #777;
}
.footer a{
  color: #777;
}
.main h2.header, .main h2.header:first-child{
    margin-top:2em;
    text-align:center;
    margin-bottom: 1em;
}

.main.container{
    padding: 0.5em 0 5em;
}

.main.container{
    font-size: 16px;
    line-height: 1.5;
}

.ui.container > .ui.menu{
    border:0;
}

.icpc.logo{
    background-image: url('img/icpc_logo_no_ibm.png');
    display:inline-block;
    min-width: 80px;
    height:auto;
    background-repeat: no-repeat;
    background-size: contain;
    background-position-y: center;
    margin: 0 10px 0 5px;
}
.icpc.logo.big {
    background-image: url('img/icpc_logo.png');
}
@media only screen and (min-width: 701px) {
    .icpc.logo.big {
        display: block;
        min-width: 250px;
        min-height: 250px;
        width: 250px;
        background-position-x: center;
        margin-left: auto;
    }
}
div.footer .engg.logo{
    background-image: url('img/engg.png');
    display:block;
    background-repeat: no-repeat;
    background-size: contain;
    min-width: 200px;
    min-height:100px;
    max-width: 558px;
    margin-top: 10px;
    width: 100%;
}
.main.ui.container > ul > li{
    margin: 0.4em 0;
}
.main.ui.container > ul li{
    margin: 0.2em 0;
}
.ui.left.rail > .ui.sticky{
    /*width: 283px !important;*/
    /*height: 212px !important;*/
    /*left: 1042px;*/
}
#followMenu{
    min-height: 212px;
}
.ui.accordion.faq div.title:not(.active) a[href]{
    opacity: 0.4;
    transition: 0.1s ease opacity;
}
.ui.accordion.faq div.title:not(.active) b{
    opacity: 0.4;
    transition: 0.1s ease opacity;
}
.ui.accordion.faq div.title:not(.active):hover a[href]{
    opacity: 1;
}
.ui.accordion.faq div.title:not(.active):hover b{
    opacity: 1;
}

.warning{
    color: #A94442;
}
.timeline .ui.steps{
    display: flex;
}
.committee dt{
    float: left;
    font-weight: bold;
}
.committee dd{
    margin-left: 220px;
    margin-bottom: 16px;
}
#committee-grid dl{
    margin: 0;
}
#committee-grid > div.column{
    padding-top: 0 !important;
    padding-bottom: 0 !important;
}
#committee-grid{
    padding-top: 2rem !important;
}
div.footer .inner.grid{
    margin: 0 0.3rem;
}
div.footer div.column.fa-container{
    padding-left: 0 !important;
    padding-right: 0 !important;
    text-align: center;
}
.sponsor span.sponsor{
    padding-top: 1.7rem;
    padding-bottom: 1.7rem;
    font-size:1.3em;
    color:#777;
    line-height:1.1em;
}
.strike{
    text-decoration: line-through;
}
.no-strike{
    text-decoration: none;
}